import styled from "styled-components";


export const StyleTela1 = styled.div`
    display: flex;

    justify-content: center;
    align-items: flex-start;
    padding: 100px 0 50px 0;
    width: 100%;
    
    background-size: cover;
    background-position: center right;
    background-repeat: no-repeat;


    .contTxt{
        display: flex;
        width: 700px;
        justify-content: center;
        flex-direction: column;
        transform: translate(-20px, 0);
    }
    .Text{
        width: 640px;
        transform: translate(0px, 0);
            h2{
                display: flex;
                align-items: center;
            }
            main{
                width: 50px;
                height: 50px;
                margin: 0px 10px 0 0;
                border-radius: 100px;
                border: 10px solid #14D895;
            }

            h4{
                font-weight: 400;
                margin: 0 0 0 60px;
                line-height: 1.5;
                span{
                    margin: 0 10px 0 0;
                    font-weight: bold;
                    color: #14D895;
                }
            }
    }
    .Beneficios{
        width: 100%;
        min-height: 300px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        img{
            position: absolute;
            right:0;

            height: 600px;
        }
        main{
            width: 50px;
            height: 50px;
            margin: 0px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }
        .texto{
            transform: translate(0px, 0px);
                h2{
                    display: flex;
                    align-items: flex-start;
                    line-height: 1;
                }
        }

        .CardIco{
            transform: translate(0px, 40px);
            display: flex;
            gap: 30px;
            .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 60px;
                        height: 60px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }

            .ico1{
                img{
                    width: 60px;
                    height: 80px;
                }
            }

            .ico2{
                img{
                    width: 60px;
                    height: 65px;
                }
            }
        }
    }
    .img{
        img{
            width: 650px;
        }
    }
    .img2{
        display: none;
            img{
                width: 300px;
            }
        }
    @media (max-width: 540px){
        flex-direction: column;

        .contTxt{
        display: flex;
        width: 100%;
        justify-content: center;
        flex-direction: column;
        transform: translate(0px, 0);
        }
        .Text{
            width:100%;
            transform: translate(0px, 0px);

            h2{
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 30px;
            }
            main{
                width: 25px;
                height: 25px;
                margin: 0px 10px 0 0;
                border-radius: 100px;
                border: 5px solid #14D895;
            }

            h4{
                font-weight: 400;
                margin: 0 20px 0 20px;
                line-height: 1;
                font-size: 16px;
                span{
                    margin: 0 10px 0 0;
                    font-weight: bold;
                    color: #14D895;
                }
            }
        }
        .Beneficios{
        width: 100%;
        min-height: 300px;
        padding: 50px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img{
            position: absolute;
            right:0;

            height: 600px;
        }
        .texto{
            transform: translate(0px, 0px);
                h2{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    line-height: 1;
                    font-size: 30px;
                    main{
                        width: 25px;
                        height: 25px;
                        margin: 5px 5px 0 0;
                        border-radius: 100px;
                        border: 5px solid #14D895;
                    }
                }
        }
        .CardIco{
            transform: translate(0px, 40px);
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            gap: 30px;
            .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 50px;
                        height: 50px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }
            h5{
                font-size: 16px;
            }
            .ico1{
                img{
                    width: 60px;
                    height: 80px;
                }
            }

            .ico2{
                img{
                    width: 60px;
                    height: 65px;
                }
            }
        }
        }
        .img{
            display: none;
            img{
                width: 300px;
            }
        }
        .img2{
            width: 100%;
            display: block;
            display: flex;
            justify-content: center;
            align-items: center;
            img{
                width: 300px;
            }
        }

    }

`