import React from 'react'
import { ContHead } from './Style';

import Logo from './img/Logo.svg'
import { Link } from 'react-router-dom';
import {Link as Scroll} from 'react-scroll'
import { RxHamburgerMenu } from 'react-icons/rx';


function Header() {

function Open(){
        var menu = document.querySelector('.Btn')
        var list = document.querySelector('.list')
            menu.classList.toggle('active')
            list.classList.toggle('open')
        
}



  return (
    <ContHead>

        <nav className="navbar">
            <Link to='/'><img src={Logo} className='logo' /></Link>

            <label className='Btn' onClick={Open}>
                <RxHamburgerMenu/>
            </label>

            <div className="menu">
                <ul className='list'>
                    <li translate='no'><Link to='/'><h5>Home</h5></Link></li>
                    <li><Link to='/Sobre'><h5>Quem Somos</h5></Link></li>
                    
                    <li>
                    <Link to='/Solucao'><h5>Nossas Solues</h5></Link>
                            <ul>
                                <Link to='/Produtos'><li><h5>Produtos</h5></li></Link>
                                <Link to='/Tecnologia'><li><h5>Tecnologia</h5></li></Link>
                                <Link to='/Auxiliar'><li><h5>Servios Auxiliares</h5></li></Link>
                            </ul>
                        </li>
                        <Link to='/Contato'><li><h5>Contato</h5></li></Link>
                </ul>
            </div>
        </nav>
        

    </ContHead>
  )
}

export default Header