import styled from "styled-components";

import img from './img/Back2.png'

export const StyleTela3 = styled.div`
    width: 100%;
    padding: 100px 20px 50px 20px;
    min-height: 664px;
    display: flex;
    justify-content: flex-end;
    .Text{
        text-align: justify;
        width: 600px;
        transform: translate(-150px,0px);
        main{
            width: 40px;
            height: 40px;
            margin: 0 20px 0 0;
            border-radius: 100px;
            border: 7px solid #14D895;
        }
        h2{
            display: flex;
            justify-self: center;
            align-items: center;
            font-size: 32px;
        }
        h4{
            font-weight: 300;
            padding: 0 50px 0 10px;
            transform: translate(50px, 20px);
        }
        img{
            margin: 50px 0 0 60px;
            width: 90%;
        }
    }

    .img{
        img{
            position: absolute;
            transform: translate(0, -65px);
            left: 0;
            width: 500px;
        }
    }


    @media (max-width: 540px){
        width: 100%;
        padding: 50px 0px 30px 0px;
        min-height: 664px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        
        .Text{
            text-align: justify;
            width: 100%;
            padding: 0 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 0 0 50px 0;
            transform: translate(0px,0px);
            main{
                width: 23px;
                height: 20px;
                margin: -30px 10px 0 0;
                border-radius: 100px;
                border: 5px solid #14D895;
            }
            h2{
                display: flex;
                justify-self: center;
                align-items: center;
                font-size: 25px;
            }
            h4{
                font-weight: 300;
                padding: 0 0px 0 0;
                transform: translate(0px, 20px);
            }
            img{
                margin: 50px 0 0 0px;
                width: 100%;
            }
        }
        .img{
            img{
                display: none;
                position: absolute;
                transform: translate(0, -65px);
                left: 0;
                width: 700px;
            }
        }
        .Img2{
            background-image: url(${img});
            background-position: center top;
            background-repeat: no-repeat;
            width: 100%;
            height: 300px;
        }
    }
`