import React from 'react'
import { StyleTela1 } from './Style'

 import ico1 from './ico/ico1.png'
 import ico2 from './ico/ico2.png'
 import ico3 from './ico/ico3.png'
 import ico4 from './ico/ico4.png'
 import app from './img/App.png'



const Tela1 = () => {
  return (
    <StyleTela1>
        <div className="contTxt">
                <div className="Text">
                        <h2> <main/><b>App 123Tele</b> </h2><br />


                        <h4>O aplicativo 123Tele foi pensado para facilitar e agilizar o acesso  sade. Com ele, o agendamento e a realizao de teleconsultas  feito com apenas alguns cliques, 24 horas por dia, 7 dias por semana.
                        <br /><br /></h4>








                </div>

                <div className="img2">
                  <img src={app} alt="" />
                </div>

                <div className="Beneficios">

                 <div className="texto">
                <h2> <main/><b>Vantagens</b> </h2>
                </div>

                <div className="CardIco">
                  <div className="Ico ico1">
                  <div className="fundo"><img src={ico1} alt="" /></div>
                    <h5>Consultas na palma da sua mo</h5>
                  </div>
                  <div className="Ico ico2">
                  <div className="fundo"><img src={ico2} alt="" /></div>
                    <h5>Democratizao
                        do acesso 
                        sade
                        </h5>
                  </div>

                  <div className="Ico">
                  <div className="fundo"><img className='img6' src={ico3} alt="" /></div>
                    <h5>Atendimento
  especializado,
  rpido e seguro</h5>
                  </div>

                  <div className="Ico">
                    <div className="fundo"><img src={ico4} alt="" /></div>
                    
                    <h5>24h por dia,
  7 dias por
  semana</h5>
                  </div>

                </div> 


                </div>
        </div>

         <div className="img">
                <img src={app} alt="" />
        </div> 
    </StyleTela1>
  )
}

export default Tela1
