import styled from "styled-components";

import img from './img/back.png'

export const StyleTela1 = styled.div`



    .Container{
        display: flex;
        flex-direction: column;
        height: 100%;
        align-items: center;
        background-image: none;
    }
    h3{
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 300;
        
        text-align: justify;
        main{
            width: 30px;
            height: 30px;
            margin: 0 0px 0 0;
            border-radius: 100px;
            border: 6px solid #14D895;
        }
        b{
            margin: 0 0 0 15px ;
        }
    }
    h5{
        font-weight: 400;
        font-size: 16px;
    }
    .cards{
        margin: 50px 0;
        display: flex;
        .txt{
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            width: 330px;
            height: 300px;
            background-color: #EEEBEB;
            padding: 30px 0 20px 0;
            margin: 25px 0;
            border-radius: 40px;
            -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.42); 
            box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.42);
            img{
                margin: 0 0 20px 0;
                width: 100px;
            }
        }
        .card1{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
        }
        .card2{
            margin: 0 50px;
        }

        .img1{
        
            img{
                width: 85px;
            }
        }

    }
    .Beneficios{
        width: 100%;
        min-height: 600px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        img{
            position: absolute;
            right:0;

            height: 600px;
        }

        .texto{
            transform: translate(100px, -50px);
                h2{
                    display: flex;
                    align-items: flex-start;
                    line-height: 1;
                }
        }

        .CardIco{
            transform: translate(110px, 0px);
            display: flex;
            gap: 50px;
            .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 70px;
                        height: 70px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }
        }
    }
    .containertxt{
    padding: 50px 0;
    height: 600px;
    background-image: url(${img});
    background-size: cover;
    background-position: center center;
    }
    .Text{
        width: 700px;
        transform: translate(100px, 0);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        main{
            width: 40px;
            height: 40px;
            border-radius: 100px;
            margin: 10px 10px 0 0;
            border: 7px solid #14D895;
        }
        h2{
            display: flex;
        }
        h4{
            font-weight: 300;
            margin: 0 50px 0 50px;
        }
        img{
            margin: 50px 0 0 50px;
            width: 90%;
        }
    }

    


    @media (max-width: 540px){
        .containertxt{
            padding: 50px 0 10px 0;
            height: 100%;
            background-image: url(${img});
            background-size: cover;
            background-position: center left;
        }
        .Text{
            padding:  0 0 50px 0;
            width: 100%;
            transform: translate(0px, 0);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
            main{
                width: 20px;
                height: 20px;
                border-radius: 100px;
                margin: 10px 10px 0 0;
                border: 5px solid #14D895;
            }
            h2{
                display: flex;
                font-size: 30px;
            }
            h4{
                font-weight: 300;
                margin: 0 30px 0 30px;
                font-size: 16px;
                font-weight: 500;
            }
            img{
                margin: 50px 0 0 50px;
                width: 90%;
            }
        }
        .Container{
            display: flex;
            flex-direction: column;
            height: 100%;
            align-items: center;
            background-image: none;
        }
            h3{
                font-size: 26px;
            }
        .cards{
            margin: 0px 0;
            display: flex;
            flex-direction: column;
        .txt{
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            width: 250px;
            height: 280px;
            background-color: #EEEBEB;
            padding: 30px 0 20px 0;
            margin: 25px 0;
            border-radius: 40px;
            -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.42); 
            box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.42);
            img{
                margin: 0 0 20px 0;
                width: 100px;
            }
            h5{
                font-size: 14px;
            }
        }
        .card1{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
        }
        .card2{
            margin: 50px 0;
        }
        .img1{
        
            img{
                width: 85px;
            }
        }
        }
    }
`
