import styled from "styled-components";
import img from './img/back.png'

export const StyleSlide = styled.div`

    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 100px 0 100px 0;
    width: 100%;
    min-height: 734px;
    background-size: cover;
    background-image: url(${img});
    background-position: center right;
    background-repeat: no-repeat;
    h2{
            display: flex;
            color: #fff;
            align-items: center;
        }
        h4, h5{
            color: #fff;
        }



.contTxt{
    transform: translate(300px, 0);
    display: flex;
    width: 700px;
    justify-content: center;
    flex-direction: column;
}
.Text{
    width: 640px;

        h2{
            display: flex;
            color: #fff;
            align-items: center;
        }
        main{
            width: 50px;
            height: 50px;
            margin: 0px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }

        h4{
            font-weight: 400;
            margin: 0 0 0 60px;
            line-height: 1.5;
            span{
                margin: 0 10px 0 0;
                font-weight: bold;
                color: #14D895;
            }
        }
}
.imgProd2{
    display: none;
}
.Beneficios{
        width: 100%;
        min-height: 300px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        img{
            position: absolute;
            right:0;

            height: 600px;
        }
        main{
            width: 50px;
            height: 50px;
            margin: 0px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }
        .texto{
            
                h2{
                    display: flex;
                    align-items: flex-start;
                    line-height: 1;
                }
        }

        .CardIco{
            transform: translate(0, 40px);
            display: flex;
            gap: 50px;
            .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 60px;
                        height: 60px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }
                    .ico1{
                        img{
                            width: 60px;
                            height: 50px;
                        }
                    }
                    .ico2{
                        img{
                            width: 60px;
                            height: 60px;
                        }
                    }
                    .ico3{
                        img{
                            width: 50px;
                            height: 60px;
                        }
                    }
                    .ico4{
                        img{
                            width: 50px;
                            height: 50px;
                        }
                    }
        }
}

.imgProd{
    position: absolute;
    left: 0;
    img{
        transform: translate(0, 100px);
        width: 650px;
    }
}

@media (max-width: 540px){
    padding: 0;
    .contTxt{
    transform: translate(0px, 0);
    display: flex;
    padding: 50px 0 150px 0;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    }
    .Text{
        width: 100%;

        h2{
            display: flex;
            color: #fff;
            align-items: center;
            justify-content: center;
            font-size: 30px;
        }
        main{
            width: 25px;
            height: 25px;
            margin: 0px 10px 0 0;
            border-radius: 100px;
            border: 5px solid #14D895;
        }

        h4{
            font-weight: 400;
            padding: 0 20px;
            font-size: 18px;
            margin: 0 0 0 0px;
            line-height: 1.5;
            span{
                margin: 0 10px 0 0;
                font-weight: bold;
                color: #14D895;
            }
        }
    }
    .Beneficios{
        width: 100%;
        min-height: 300px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img{
            position: absolute;
            right:0;

            height: 600px;
        }
        
        .texto{
            
                h2{
                    display: flex;
                    align-items: center;
                    line-height: 1;
                    font-size: 30px;
                    transform: translate(0,30px);
                    main{
                        width: 25px;
                        height: 25px;
                        margin: 0px 10px 0 0;
                        border-radius: 100px;
                        border: 5px solid #14D895;
                    }
                }
        }

        .CardIco{
            transform: translate(0, 100px);
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            gap: 30px;
                .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 50px;
                        height: 50px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }
            h5{
                font-size: 16px;
            }
                    .ico1{
                        img{
                            width: 60px;
                            height: 50px;
                        }
                    }
                    .ico2{
                        img{
                            width: 60px;
                            height: 60px;
                        }
                    }
                    .ico3{
                        img{
                            width: 50px;
                            height: 60px;
                        }
                    }
                    .ico4{
                        img{
                            width: 50px;
                            height: 50px;
                        }
                    }
        }
    }
    .imgProd{
    position: absolute;
    left: 0;
    display: none;
    img{
        transform: translate(0, 100px);
        width: 650px;
    }
}
    .imgProd2{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
        img{
            transform: translate(0px, 0px);
            width: 290px;
        }
    }
}
`