import React from 'react'
import { Slide } from './Style';

import Img1 from './img/ico1.png'
import Img2 from './img/ico2.png'
import Img3 from './img/ico3.png'
import Img4 from './img/ico4.png'

import { StyleSobre } from './Style';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { Navigation} from 'swiper';

function SlidePar() {
  return (

    <Slide>

<div className="ContSwiper">
            <h2><main/><b>Parceiros THG</b></h2>
            <br /><br />
    <Swiper
        modules={[Navigation]}
        spaceBetween={150}
        breakpoints={{
            '540':{
                slidesPerView: 3
            },
           ' 1920':{
                slidesPerView:1
            }
        }}
        navigation
        loop='true'
        
        onSlideChange={() => console.log('slide change')}
        onSwiper={(swiper) => console.log(swiper)}
        >


        <SwiperSlide>
            <div className='Card'>
                <img className='img2' src={Img1}/>
            </div>
        </SwiperSlide>

        <SwiperSlide>
            <div className='Card'>
                <img src={Img2}/>
            </div>
        </SwiperSlide>

        <SwiperSlide>
            <div className='Card'>
                <img src={Img3}/>
            </div>
        </SwiperSlide>

        <SwiperSlide>
            <div className='Card'>
                <img className='img1' src={Img4}/>
            </div>
        </SwiperSlide>

        
    </Swiper>
</div>
        



        {/* <div className='slider'>
            <div className='slider-track'>




                <div className='Card'>
                    <img src={Img1}/>
                </div>
                
                
                
            </div>
        </div> */}
    </Slide>
    
  )
}

export default SlidePar