import React from 'react'
import { StyleAp } from './Style';

import img1 from './ico/ico1.png'
import img2 from './ico/ico2.png'
import img3 from './ico/ico3.png'
import img4 from './ico/ico4.png'
import img5 from './ico/ico5.png'
import img6 from './ico/ico6.png'
import img7 from './ico/ico7.png'



const Aparelhos = () => {
  return (
    <StyleAp>
        <h2><main/><b>Nossos Aparelhos</b> </h2>


        <div className="cards">
            <div className="cardsL">
                <div className="cardL">
                        <img src={img1} alt="" />
                        <h4>Eletrocardigrafo Mvel</h4>
                    </div>
                <div className="cardL">
                    <img src={img2} alt="" />
                    <h4>Monitor Holter</h4>
                    </div>
                <div className="cardL">
                    <img src={img3} alt="" />
                    <h4>Eletroencefalograma</h4>
                    </div>
                <div className="cardL">
                    <img src={img4} alt="" />
                    <h4>Raio X Mvel</h4>
                    </div> 
            </div>
                
            <div className="cardsR">
                    <div className="cardR"><img src={img5} alt="" /><h4>Espirmetro</h4> </div>
                    <div className="cardR"><img src={img6} alt="" /><h4>Monitor para Presso  arterial MAPA</h4> </div>
                    <div className="cardR"><img src={img7} alt="" /><h4>Ecgrafo Porttil com Doppler Colorido
        </h4>
                    </div>
            </div>
        </div>

    </StyleAp>
  )
}

export default Aparelhos