import styled from "styled-components";

import Fundo from './img/back.png'

export const StyeTelaa2 = styled.div`
background-image: url(${Fundo});
background-size: cover;
width: 100%;


    .Beneficios{
        width: 100%;
        min-height: 600px;
        padding: 100px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        h2{
            color: #fff;
            
        }
        img{

        }
        .texto2{
            display: none;
        }
        .texto{
            transform: translate(0px, -50px);
            display: flex;
            justify-content: center;
            align-items:center;
                h2{
                    display: flex;
                    line-height: 1;
                    justify-content: center;
                    align-items: center;
                }
        }

            .CardIco{
                
                display: flex;
                gap: 30px 60px;
                flex-wrap: wrap;
                justify-content: center;

                .break {
                    flex-basis: 100%;
                    height: 0;
                }
                .Ico{   
                        width: 200px;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-start ;
                        align-items: center;
                        text-align: center;
                        color: #fff;
                        .fundo{
                                width: 150px;
                                height: 150px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background-color:#14D895 ;
                                border-radius: 100px;
                                margin: 0 0 20px 0;
                        }
                        img{
                            width: 70px;
                            position: relative;
                        }
                        .img6{
                            transform: translate(-5px, 0);
                        }
                }
                .ico6{
                        .fundo{
                            img{
                                width: 50px;
                            }
                        }
                }
                .ico3{
                        .fundo{
                            img{
                                margin: 0 -5px 0 0 ;
                            }
                        }
                }

            }
        }
        main{
            width: 50px;
            height: 50px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }


        @media (max-width: 540px){
            .Beneficios{
                .texto{
                    display: none;
                    h2{
                        font-size: 20px;
                    }
                    main{
                        width: 30px;
                        height: 25px;
                        margin: -10px 10px 0 25px;
                        border-radius: 100px;
                        border: 5px solid #14D895;
                    }
                }
                .texto2{
                    display: flex;
                    h2{
                        font-size: 26px;
                        text-align: center;
                        transform: translate(0, -65px);
                    }
                    main{
                        width: 25px;
                        height:25px;
                        transform: translate(20px, 28px);
                        border-radius: 100px;
                        border: 5px solid #14D895;
                    }
                }
                h5{
                    font-size: 16px;
                }
                .CardIco{
                
                    display: flex;
                    gap: 30px 30px;
                    flex-wrap: wrap;
                    justify-content: center;

                .break {
                    flex-basis: 100%;
                    height: 0;
                }
                .Ico{   
                        width: 130px;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-start ;
                        align-items: center;
                        text-align: center;
                        color: #fff;
                        .fundo{
                                width: 130px;
                                height: 130px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background-color:#14D895 ;
                                border-radius: 100px;
                                margin: 0 0 20px 0;
                        }
                        img{
                            width: 70px;
                            position: relative;
                        }
                        .img6{
                            transform: translate(-5px, 0);
                        }
                }
                .ico6{
                        .fundo{
                            img{
                                width: 50px;
                            }
                        }
                }
                .ico3{
                        .fundo{
                            img{
                                margin: 0 -5px 0 0 ;
                            }
                        }
                }

            }
            }

        }
`