import styled from "styled-components";

import img from './img/back.png'

export const StyeTelaa2 = styled.div`
display: flex;
width: 100%;


.Beneficios{
        width: 700px;
        min-height: 600px;
        padding: 100px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        
        h2{
            color: #000;
            text-align: center;
        }
        h4{
            padding: 0 0 0 50px ;
            font-weight: 400;
        }
        img{

        }

        .texto{
            transform: translate(30px, -50px);
                h2{
                    display: flex;
                    align-items: flex-start;
                    line-height: 1;
                    justify-content: center;
                    align-items: center;
                }
                h4{
                    transform: translate(80px, 20px);
                }
        }

        .CardIco{
            transform: translate(100px,0);
            display: flex;
            gap: 40px 20px;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0;
            .break {
                flex-basis: 100%;
                height: 0;
            }
            .Ico{   
                    width: 150px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    color: #000;
                    .fundo{
                            width: 150px;
                            height: 150px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 60px;
                        height: 60px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }
        }
    }

        main{
            width: 50px;
            height: 50px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }

    .img{
            background-image: url(${img});
            right: 0;
            width: 600px;
            height: 805px;
            transform: translate(0, 20px);
            position: absolute;
    }   


@media (max-width: 540px){
    .img{
        display: none;
    }


    .Beneficios{
        width: 100%;
        min-height: 600px;
        padding: 50px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        h2{
            color: #000;
            text-align: center;
        }
        
        h4{
            padding: 0 0 0 0px ;
            font-weight: 400;
        }
        img{

        }

        .texto{
            transform: translate(0,0);
                h2{
                    display: flex;
                    align-items: center;
                    line-height: 1;
                    justify-content: center;
                    font-size: 30px;
                    margin: 0 0 20px 0;
                    padding: 0 100px;
                    main{
                    width: 25px;
                    height: 25px;
                    position: absolute;
                    transform: translate(-70px,-20px);
                    border-radius: 100px;
                    border: 5px solid #14D895;
                }
                }
                h4{
                    transform: translate(0,0);
                    padding: 0 20px;
                }
        }

        .CardIco{
            transform: translate(0px,0);
            display: flex;
            gap: 30px 30px;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0;
            padding: 50px 0 0px 0;
            .break {
                flex-basis: 100%;
                height: 0;
            }
            .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    color: #000;
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 60px;
                        height: 60px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }
        }
    }
}

@media (max-width: 350px){
    .Beneficios{

        .texto{
                transform: translate(0,0);
                    h2{
                        display: flex;
                        align-items: center;
                        line-height: 1;
                        justify-content: center;
                        font-size: 30px;
                        margin: 0 0 20px 0;
                        padding: 0 50px;
                        main{
                        width: 25px;
                        height: 25px;
                        position: absolute;
                        transform: translate(-70px,-20px);
                        border-radius: 100px;
                        border: 5px solid #14D895;
                    }
                    }
                    h4{
                        transform: translate(0,0);
                        padding: 0 20px;
                    }
            }
    }
}
`