import React from 'react'
import { StyleTela1 } from './Style'

import img1 from './img/img1.png'
import img2 from './img/img2.png'
import img3 from './img/img3.png'
import img4 from './img/img4.png'
import img5 from './img/img5.png'
import img6 from './img/img6.png'


const Tela1 = () => {
  return (
    <StyleTela1>
            <div className="Text">
                    <h2> <main/><b>Referncia em Autoatendimento</b> </h2><br />


                    <h4>Nossas solues em sade oferecem assistncia local evitando o encaminhamento dos pacientes para outro servio de sade sem necessidade. <br />

                    A assertividade dos nossos servios proporciona economicidade assistencial, uso racional de medicamentos e reduo de tempo de internao dos pacientes hospitalizados.
                    Oferecemos projetos personalizados, focados na sua realidade. Veja como o nosso processo de produo funciona:

                    </h4>
            </div>


            <div className="Beneficios">
            <div className="main"><main></main></div>
                <div className="box box1">
                        <img src={img1} alt="" />
                        <h4>1. Requerimento</h4>
                </div>

                <div className="box box2 img2">
                        <img src={img2} alt="" />
                        <h4>2. Design</h4>
                </div>

                <div className="box box3">
                        <img src={img3} alt="" />
                        <h4>3. Projeto</h4>
                </div>

                <div className="box box4 img4">
                        <img src={img4} alt="" />
                        <h4>4. Prototipao</h4>
                </div>

                <div className="box box5 ">
                        <img src={img5} alt="" />
                        <h4>5. Produo</h4>
                </div>

                <div className="box box6">
                        <img src={img6} alt="" />
                        <h4>6. Qualidade</h4>
                </div>
                

                
            </div>

            
    </StyleTela1>
  )
}

export default Tela1
