import styled from 'styled-components';


export const Slide = styled.div`
  min-height: 400px;
  display: grid;
  place-items: center;


h1{
    font-size: 51px;
    font-weight: 700;
    color: #004584;
}
.TitlePar{
    h2{
        margin: 30px 0 0 0;
        transform: translate(-150px, 0);
        main{
                width: 30px;
                height: 30px;
                margin: 0px 10px 0 0;
                border-radius: 100px;
                border: 5px solid #14D895;
            }
    }
}
.slider{
    height: 250px;
    margin: auto;
    position: relative;
    width: 100%;
    max-width: 1820px;
    display: grid;
    place-items: center;
    overflow: hidden;
    .slider-track{
        display: flex;
        width: calc(200px * 46);
        animation: scroll 35s linear infinite;
    }
    @keyframes scroll {
        0%{
            transform: translatex(0);
        }
        100%{
            transform: translatex(calc(-250px * 19));
        }
    }
}
  
.Card{
    margin: 20px;
    width: 200px;
    height: 150px;
    background-color: #1B1F6B;
    border-radius: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
        transform: translate(0, 0);
        margin: 0;
        width: 150px;
    }
}

@media (max-width: 900px){
    transition: all .50s;
h1{
    font-size: 41px;
}
}
@media (max-width: 700px){
    transition: all .50s;
    text-align: center;
h1{
    font-size: 27px;
}
}
@media (max-width: 540px){

    .Card{
        width: 200px;
        height: 150px; 
    }
    .TitlePar{
    h2{
        transform: translate(-50px, 0);
        main{
                width: 20px;
                height: 20px;
                margin: 0px 10px 0 0;
                border-radius: 100px;
                border: 5px solid #14D895;
            }
    }
} 
        
}
`;
