import React from 'react'
import { StyledInfos } from './Style';

import ico1 from './img/ico1.png'
import ico2 from './img/ico2.png'
import ico3 from './img/ico3.png'
import mapa from './img/img.png'

const Infos = () => {
  return (
    <StyledInfos>
        
        
        <div className="textos">
            <div className="box">
                <div className="ico"><img src={ico1} alt="" /></div>
                <h4>(11) 3821-5300</h4>
            </div>
            <div className="box">
                <div className="ico"><img src={ico2} alt="" /></div>
                <h4>contato@123tele.med.br</h4>
            </div>
            <div className="box">
                <div className="ico"><img src={ico3} alt="" /></div>
                <h4>Rua da Consolao, 1681 <br />
                        2 Andar Consolao<br />
                        So Paulo  SP - 01301-100
                    </h4>
            </div>

        </div>


        <div className="Mapa">
            <a href="https://www.google.com.br/maps/place/R.+da+Consola%C3%A7%C3%A3o,+1681+-+2%C2%BA+Andar+-+Consola%C3%A7%C3%A3o,+S%C3%A3o+Paulo+-+SP,+01301-100/@-23.5517437,-46.6587797,17z/data=!3m1!4b1!4m5!3m4!1s0x94ce5833e5331735:0x995506dc06e5b00f!8m2!3d-23.5517437!4d-46.656591" target='_blank'>
            <img src={mapa} alt="" /></a>
        </div>
    </StyledInfos>
  )
}

export default Infos