import styled from "styled-components";

import Fundo from './img/back.png'


export const StyeTelaa2 = styled.div`
padding: 100px 0;
width: 100%;
min-height: 870px;

        .Beneficios{
            width: 100%;
            min-height: 650px;
            padding: 50px 0;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            h2{
                color: #000;
                font-weight: 400;
            }
            img{
            }
            a{
                color: #fff;
                text-decoration: none;
            }
            .texto{
                    transform: translate(160px,0);
                    h2{
                        display: flex;
                        align-items: flex-start;
                        line-height: 1;
                        justify-content: center;
                        align-items: center;
                    }
            }

            .CardIco{
                transform: translate(-150px,0);
                margin: 50px 0 0 0;
                display: flex;
                gap: 30px 60px;
                flex-wrap: wrap;
                justify-content: center;

                h4{
                    font-weight: 400;
                }
                .break {
                    flex-basis: 100%;
                    height: 0;
                }
                .Ico{   
                        width: 200px;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-start ;
                        align-items: center;
                        text-align: center;
                        color: #000;
                        .fundo{
                                width: 150px;
                                height: 150px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background-color:#14D895 ;
                                border-radius: 100px;
                                margin: 0 0 20px 0;
                        }
                        img{
                            width: 70px;
                            position: relative;
                        }
                        .img6{
                            transform: translate(-5px, 0);
                        }
                }
            }
            .imgBack{
                    background-image: url(${Fundo});
                    background-position: center center;
                    background-size: cover;
                    transform: translate(0, -130px);
                    right: 0;
                    position: absolute;
                    width: 700px;
                    height: 850px;
            }
        }
        main{
            width: 50px;
            height: 50px;
            margin: 12px 20px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }


        @media (max-width: 540px){
            padding: 50px 0;
            min-height: 100%;
            .Beneficios{
                width: 100%;
                min-height: 100%;
                padding: 50px 0;
                display: flex;
                flex-direction: column;
                align-items: center;

                h2{
                    color: #000;
                    transform: translate(-20px, 0);
                    font-weight: 400;
                }
                img{
                }
                .texto{
                        transform: translate(20px,0);
                        h2{
                            display: flex;
                            align-items: flex-start;
                            line-height: 1;
                            font-size: 28px;
                            justify-content: center;
                            align-items: center;
                            main{
                                width: 30px;
                                height: 30px;
                                margin: 0px 10px 0 0;
                                border-radius: 100px;
                                border: 5px solid #14D895;
                            }
                        }
                }
                .CardIco{
                    transform: translate(0px,0);
                    margin: 50px 0 0 0;
                    display: flex;
                    gap: 20px 30px;
                    flex-wrap: wrap;
                    justify-content: center;

                    h4{
                        font-weight: 400;
                        text-align: center;
                    }
                    .break {
                        flex-basis: 100%;
                        height: 0;
                    }
                    .Ico{   
                            width: 130px;
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-start ;
                            align-items: center;
                            text-align: center;
                            color: #000;
                            .fundo{
                                    width: 130px;
                                    height: 130px;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    background-color:#14D895 ;
                                    border-radius: 100px;
                                    margin: 0 0 20px 0;
                            }
                            img{
                                width: 60px;
                                position: relative;
                            }
                            .img6{
                                transform: translate(-5px, 0);
                            }
                    }
                }
                .imgBack{
                    display: none;
                    background-image: url(${Fundo});
                    background-position: center center;
                    background-size: cover;
                    transform: translate(0, -130px);
                    right: 0;
                    position: absolute;
                    width: 700px;
                    height: 850px;
                }
            }
        }
`