import styled from "styled-components";

import img from './img/back.png'
import img2 from './img/back2.png'

export const StyleTela1 = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 100px 0;
    width: 100%;
    background-image: url(${img});
    background-size: cover;
    background-position: center right;
    background-repeat: no-repeat;
    z-index: -1; 

.Text{
    width: 940px;
    transform: translate(150px, 0);


        h2{
            display: flex;
            align-items: center;
        }
        main{
            width: 50px;
            height: 50px;
            margin: 0px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }

        h4{
            font-weight: 400;
            margin: 0 0 0 60px;
            width: 650px;
            line-height: 1.5;
            text-align: justify;
            span{
                margin: 0 10px 0 0;
                font-weight: 800;
                color: #14D895;
            }
            b{
                font-weight: 400;
                margin: 0 0 0 20px;
            }
        }
        .txt2{
            display: none;
        }
}


.Beneficios{
    margin: 50px 0 0 0;
    width: 100%;
    max-width: 1140px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    
    .main{
        transform: translate(0, -50px);
        display: flex;
        align-items: center;
        position: absolute;
        width: 1100px;
        height: 7px;
        background-color: #1A1F6B;
        border-radius: 10px;
        main{
            width: 25px;
            height: 25px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            transform: translate(-35px, -5px);
            border: 5px solid #14D895;
        }
    }

    .box{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        img{
            width: 60px;
        }
        h4{
            margin: 60px 0 0 0;
        }
    }
    .img2, .img4{
        img{
            width: 80px;
        }
    }
}

@media (max-width: 540px){
    background-image: url(${img2});
    padding: 50px 0 ;
    .Text{
        z-index: 1;
        width: 100%;
        transform: translate(0px, 0);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h2{
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding: 0 20px;
            margin: 0 0 0 15px;
            font-size: 30px;
        }
        main{
            width: 25px;
            height: 25px;
            transform: translate(-140px, -15px);
            position: absolute;
            border-radius: 100px;
            border: 5px solid #14D895;
        }

        h4{
            font-weight: 400;
            margin: 0  0 0 0px;
            width: 320px;
            padding: 0 20px;
            font-size: 16px;
            line-height: 1.5;
            text-align: left;
            
            span{
                margin: 0 10px 0 0;
                font-weight: 800;
                color: #14D895;
            }
            b{
                font-weight: 400;
                margin: 0 0 0 16px;
            }
        }
        .txt1{
            display: none;
        }
        .txt2{

            display: block;
        }
        }
}
`