import React from 'react'

import '../../styles/Main.css'

import { StyleHome } from './Style';
import Header from './../../Templates/Header/Index';
import Tela1 from './Tela1/Index';
import Tela2 from './Tela2/Index';
import Tela3 from './Tela3/Index';
import Footer from './../../Templates/Footer/Index';
import { Link } from 'react-router-dom';

const Home = () => {
  return (
    <StyleHome>


           <div className="Cont">
            <Header/>
            <div className="Container">

                <div className="text"><br /><br /><br />
                  <h1>123Tele</h1>
                  <h3>Somos a 123Tele, uma empresa especializada
                        em oferecer <b> assistncia e servios de telemedicina </b>  
                        com agilidade e segurana aos nossos clientes.
                        Trabalhamos com atendimento humanizado
                        e recursos tecnolgicos inovadores atravs de
                        <b> APP, totem e cabine de autoatendimento.</b></h3>

                  <button>
                    <Link to='/Sobre'>Saiba Mais</Link>
                  </button>
                </div>
            </div>
          </div>
          
        <Tela3/>
        
        <Tela1/>
        <Tela2/>
        <div className="final">
          <div className="IMG"></div>
        </div>
        <Footer/>
    </StyleHome>
  )
}

export default Home
