import React from 'react'
import HomeContato from './HomeContato/Index';

import Form from './Form/Index';
import Infos from './Infos/Index';
import Footer from './../../Templates/Footer/Index';
import { StyleForm } from './Style';

const Contato = () => {
  return (
    <StyleForm>
          <HomeContato/>
           <div className="back">
            <Form/>
            <Infos/>
          </div> 
          
          <Footer/>
    </StyleForm>
  )
}

export default Contato
