import styled from "styled-components";

import back from './img/back.png'

export const StyleTela3 = styled.div`
        width: 100%;
        min-height: 800px;
        background-image: url(${back});
        background-position: center center;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        .title2{
            display: none;
        }
        main{
            width: 50px;
            height: 50px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }
        .Text{
            transform: translate(100px, 0 );
            
            h1{
                display: flex;
                line-height: 1.1;
                color: #fff;
            }
            h2{
                margin: 0 0 0 60px;
                display: flex;
                line-height: 1.1;
                color: #fff;
            }
            h3{
                display: flex;
                line-height: 1.1;
                color: #fff;
                font-size: 24px;
                font-weight: 300;
                width: 700px;
                margin: 30px 0 0 60px;
            }
            button{
                background-color: #14D895;
                border: none;
                transform: translate(65px, 0);
                padding: 10px 30px;
                border-radius: 30px;
                -webkit-box-shadow: 0px 10px 13px -7px #cbcbcb, 5px 5px 15px 5px rgba(203,203,203,0); 
                box-shadow: 0px 10px 13px -7px #cbbcbc, 5px 5px 15px 5px rgba(203,203,203,0);
                a{
                    text-decoration: none;

                }
            }
        }
        .txtgrup{
            display: flex;
            margin: 50px 0;
            transform: translate(60px, 0);


            h4{
                span{
                    color:#14D895 ;
                    
                    margin: 0 10px 0 0;
                    font-weight: 800;
                }
                b{
                    font-weight: 400;
                    margin: 0 0 0 15px;
                }
                font-weight: 400;
                color: #fff;
            }
            .txt1{
                margin: 0 50px 0 0;
            }
        }

    @media (max-width: 540px){
        .Text{
            transform: translate(0px, 0 );
        
        h1{
            display: flex;
            line-height: 1.1;
            color: #fff;
            font-size: 24px;
            
            main{
            width: 23px;
            height: 23px;
            margin: 2px 10px 0 15px;
            transform: translate(90px,-3px);
            border-radius: 100px;
            border: 5px solid #14D895;
        }
        }
       
        h3{
            display: flex;
            line-height: 1.1;
            color: #fff;
            font-size: 24px;
            font-weight: 300;
            width: 100%;
            padding: 0 20px;
            margin: 30px 0 0 0px;
        }
        button{
            background-color: #14D895;
            border: none;
            transform: translate(65px, 0);
            padding: 10px 30px;
            border-radius: 30px;
            -webkit-box-shadow: 0px 10px 13px -7px #cbcbcb, 5px 5px 15px 5px rgba(203,203,203,0); 
            box-shadow: 0px 10px 13px -7px #cbbcbc, 5px 5px 15px 5px rgba(203,203,203,0);
            a{
                text-decoration: none;

            }
        }
        }
        .title1{
            display: none;
        }
        .title2{
            width: 100%;
            padding: 50px 0 0 0px;
            display: flex;
            text-align: center;
            h1{
                transform: translate(-10px, 0);
            }
        }
        .txtgrup{
            display: flex;
            flex-direction: column;
            margin: 50px 0;
            transform: translate(0px, 0);
            padding: 0 0 0 25px;


        h4{
            font-size: 16px;
            padding: 0 0px 0 0;
            text-align: left;
            span{
                color:#14D895 ;
                margin: 0 10px 0 0;
                font-weight: 800;
            }
            b{
                font-weight: 400;
                margin: 0 0 0 15px;
            }
            font-weight: 400;
            color: #fff;
        }
        .txt1{
            margin: 0 50px 0 0;
        }
    }


    }
`