import styled from "styled-components";

export const StyledInfos = styled.div`

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0;

.textos{
    display: flex;
    flex-direction: column;
    transform: translate(0px, 0);
    .box{
        width: 500px;
        display: flex;
        align-items: center;
        margin: 50px;
        img{
            width: 80px;
            margin: 0 20px 0 0;
        }
    }
}


.Mapa{
    img{
        width: 600px;
        transform: translate(-30px, 0);
    }
}


@media (max-width: 540px){
    flex-direction: column;
    padding: 20px 20px 20px 20px;
    .textos{
    display: flex;
    flex-direction: column;
    transform: translate(0px, -30px);

    .box{
        width: 100%;
        display: flex;
        align-items: center;
        margin: 10px 0;

        h4{
            font-size: 16px;
        }
        img{
            width: 40px;
            margin: 0 20px 0 0;
        }
    }
    }
    .Mapa{
        img{
            width: 80%;
            transform: translate(35px, 0);
        }
    }

}
`