import React from 'react'
import { StyleTela3 } from './Style'

const Tela3 = () => {
  return (
    <StyleTela3>
      <section id='Diferenciais'>
            <div className="Text">

              <div className="title1">
                <h1><main/>Nosso Diferencial <br /></h1>
                <h2>Medidos Multiparamtros</h2>
              </div>

              <div className="title2">
                <h1><main/>Nosso <br />Diferencial
                Medidor de parmetro: <br /> Medidos Multiparamtros</h1>
              </div>
            




            <h3>Os servios da 123Tele oferecem uma soluo nica
             no mercado da telemedicina. <br /> Os nossos totens
              e cabines contam com Medidor Multiparmetros
               , aparelho de alta tecnologia capaz de analisar
                em tempo real as condies de sade do paciente.</h3>

                 <div className="txtgrup">
                  <div className="txt1">
                    <h4><span></span>Pulso</h4>
                    <h4><span></span>SpO2  Oximetria de Pulso</h4>
                    <h4><span></span>PA  Presso Arterial</h4>
                    <h4><span></span>Hgb  Hemoglobina</h4>
                    <h4><span></span>Hct  Hematcrito</h4>
                    <h4><span></span>O2</h4>
                    <h4><span></span>CO2</h4>
                    <h4><span></span>BV  Viscosidade Sangunea</h4>
                    <h4><span></span>MAP  Presso Arterial Mdia</h4>
                    
                  </div>
                  <div className="txt2">
                  <h4><span></span>CO - Dbito Cardaco</h4>
                  <h4><span></span>SV  Volume Sistlico</h4>
                  <h4><span></span>CI  ndice Cardaco</h4>
                  <h4><span></span>PCO2</h4>
                  <h4><span></span>PO2</h4>
                  <h4><span></span>PH</h4>
                  <h4><span></span>RBC  Contagem de Clulas<br /><b> Vermelhas  (Eritrcitos)</b> </h4>
                  
                  </div>
                </div> 
            </div>
      </section>      
    </StyleTela3>
  )
}

export default Tela3
