import styled from "styled-components";

export const StyledForm = styled.div`
    padding: 100px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    main{
            width: 40px;
            height: 40px;
            margin: 0 20px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
    }

    h1{
        display: flex;
        align-items: center;
    }

    form{
        width: 1140px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        input{
            width: 100%;
            background-color: #F1F0F0;
            border: none;
            outline: none;
            padding: 20px 0 20px 55px ;
            height: 50px;
            margin: 10px;
            border-radius: 10px;
        }
        label{
            margin: 5px 0 5px 65px;
            font-weight: bold;
        }
        textarea{
            background-color: #F1F0F0;
            border: none;
            outline: none;
            width: 100%;
            padding: 20px  0 20px 55px;
            height: 200px;
            margin: 10px;
            border-radius: 10px;
        }
        button{
            background-color: #1B1F6B;
            border: none;
            outline: none;
            padding: 0px  20px;
            height:50px;
            margin: 10px 0 10px 65px;
            border-radius: 10px;
            a{
                color: #fff;
                text-decoration: none;
            }
        }
    }


    @media (max-width: 540px){
        padding: 100px 20px;
        h1{
            font-size: 22px;
        }
        main{
            width: 25px;
            height: 25px;
            margin: 0 10px 0 20px;
            border-radius: 100px;
            border: 7px solid #14D895;
    }
        form{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        input{
            width: 100%;
            background-color: #F1F0F0;
            border: none;
            outline: none;
            padding: 20px 0 20px 55px ;
            height: 50px;
            margin: 10px 0;
            border-radius: 10px;
        }
        label{
            margin: 5px 0 5px 60px;
            font-weight: bold;
        }
        textarea{
            background-color: #F1F0F0;
            border: none;
            outline: none;
            width: 100%;
            padding: 20px  0 20px 55px;
            height: 200px;
            margin: 10px 0;
            border-radius: 10px;
        }
        button{
            background-color: #1B1F6B;
            border: none;
            outline: none;
            padding: 0px  20px;
            height:50px;
            margin: 10px 0 10px 50px;
            border-radius: 10px;
            a{
                color: #fff;
                text-decoration: none;
            }
        }
        }
    }
`