import React from 'react'
import { StyleTela2 } from './Style'

 import ico1 from './ico/ico1.png'
 import ico2 from './ico/ico2.png'
 import ico3 from './ico/ico3.png'
 import ico4 from './ico/ico4.png'
 import app from './img/App.png'
 import app2 from './img/App2.png'


const Tela2 = () => {
  return (
    <StyleTela2>
              <div className="contTxt">
                <div className="Text">
                        <h2> <main/><b>Totem 123Tele</b> </h2><br />


                        <h4>Agilidade, assertividade e qualidade no atendimento. <br /> So os trs benefcios principais do Totem 123Tele. O equipamento foi projetado para ocupar espaos pblicos, elevando o nvel das teleconsultas para um novo patamar.
                        <br /><br /></h4>








                </div>


                 <div className="img2">
                  <img src={app2} alt="" />
                </div>



                <div className="Beneficios">
                   <div className="texto">
                  <h2> <main/><b>Vantagens</b> </h2>
                  </div> 
                  <div className="CardIco">
                    <div className="Ico ico4">
                    <div className="fundo"><img src={ico1} alt="" /></div>
                      <h5>Agilidade no atendimento</h5>
                    </div>
                    <div className="Ico ">
                    <div className="fundo"><img src={ico2} alt="" /></div>
                      <h5>Diagnstico seguro </h5>
                    </div>

                    <div className="Ico ico3">
                    <div className="fundo"><img className='img6' src={ico3} alt="" /></div>
                      <h5>Menos tempo em filas</h5>
                    </div>

                    <div className="Ico">
                      <div className="fundo"><img src={ico4} alt="" /></div>
                      <h5>Autoatendimento assertivo</h5>
                    </div>

                  </div>
                  </div>
                </div>

         <div className="img">
                <img src={app} alt="" />
        </div>
    </StyleTela2>
  )
}

export default Tela2
