import styled from "styled-components";

import back from './img/back.png'
export const StyleTela1 = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 100px 0;

.Text{
        width: 1340px;
        transform: translate(-10px, 0);
        h2{
            display: flex;
            align-items: center;
        }
        main{
            width: 50px;
            height: 50px;
            margin: 0px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }

        h4{
            font-weight: 400;
            font-size: 22px;
            margin: 0 0 0 60px;
        }
}


.Beneficios{
    margin: 50px 0 0 0;
    width: 100%;
    max-width: 1340px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    
    .main{
        transform: translate(0, -50px);
        display: flex;
        align-items: center;
        position: absolute;
        width: 1250px;
        height: 7px;
        background-color: #1A1F6B;
        border-radius: 10px;
        main{
            width: 25px;
            height: 25px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            transform: translate(-35px, -5px);
            border: 5px solid #14D895;
        }
    }

    .box{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        img{
            width: 60px;
        }
        h4{
            margin: 60px 0 0 0;
        }
    }
    .img2, .img4{
        img{
            width: 80px;
        }
    }
}

@media (max-width: 540px){
    background-image: url(${back});
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 50px 0;
    .Text{
        transform: translate(0px, 0);
        width: 100%;
        padding: 0 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h2{
            display: flex;
            text-align: center;
            transform: translate(-15px, 0px);
            width: 250px;
            font-size: 30px;
        }
        main{
            position: absolute;
            width: 25px;
            height: 25px;
            border-radius: 100px;
            transform: translate(-5px, -18px);
            border: 5px solid #14D895;
        }

        h4{
            font-weight: 400;
            font-size: 20px;
            margin: 0 0 0 0px;
        }
    }







    
    .Beneficios{
        margin: 50px 0 0 0;
        width: 100%;
        padding: 100px 0;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        
    
    .main{
        transform: translate(0,0);
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        width: 5px;
        height: 120%;
        background-color: #1A1F6B;
        border-radius: 10px;
        main{
            width: 25px;
            height: 25px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            transform: translate(5px, -50px);
            border: 5px solid #14D895;
        }
    }

    .box{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        img{
            width: 60px;
        }
        h4{
            margin: 20px 0 0 0;
            font-size: 16px;
        }
    }
    .box1, .box3, .box5{
        transform: translate(-80px, 0);
    }
    .box2, .box4, .box6{
        transform: translate(80px, 0);
    }
    .box5{
        img{
            width: 70px;
        }
    }
    .img2, .img4{
        img{
            width: 80px;
        }
    }
}
}
`