import React from 'react'
import { StyeTelaa2 } from './Style';

import ico1 from './ico/ico1.png'
import ico2 from './ico/ico2.png'
import ico3 from './ico/ico3.png'
import ico4 from './ico/ico4.png'
import { Link } from 'react-router-dom';



const Tela2 = () => {
  return (
    <StyeTelaa2>
       <div className="Beneficios">
              

              <div className="texto">
              <h2> <main/><b>Soluo 123Tele</b> </h2>
              </div>
              <div className="CardIco">

              
              <Link to='/Produtos'>
                <div className="Ico">
                <div className="fundo"><img src={ico1} alt="" /></div>
                  <h4>App</h4>
                </div>
              </Link>
              <Link to='/Produtos'>
                <div className="Ico">
                <div className="fundo"><img src={ico2} alt="" /></div>
                  <h4>Cabine | Totem</h4>
                </div>
              </Link>

                  <div className="break"></div>
              <Link to='/Tecnologia'>
                <div className="Ico">
                <div className="fundo"><img className='img6' src={ico3} alt="" /></div>
                  <h4>Tecnologia</h4>
                </div>
              </Link>
              <Link to='/Auxiliar'>
                <div className="Ico">
                  <div className="fundo"><img src={ico4} alt="" /></div>
                  
                  <h4>Servios Auxiliares</h4>
                </div>
              </Link>

                    


                

              </div>  


               <div className="imgBack"></div>
        </div>
    </StyeTelaa2>
  )
}

export default Tela2
