import styled from "styled-components";

export const StyleTela1 = styled.div`
    .Container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 600px;
    }
        h3{
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 300;
            main{
                width: 30px;
                height: 30px;
                margin: 0 1px 0 0;
                border-radius: 100px;
                border: 7px solid #14D895;
            }
            b{
                margin: 0 0 0 15px ;
            }
        }
        h2{
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 300;
            main{
                width: 50px;
                height: 50px;
                margin: 0 20px 0 0;
                border-radius: 100px;
                border: 10px solid #14D895;
            }
            b{
                margin: 0 0 0 15px ;
            }
        }
        h2{
            main{
                margin: 0 20px 0 0;
            }
        }
        h5{
            font-weight: 400;
        }
    
    .cards{
        margin: 20px 0;
        display: flex;
        position: relative;
        .txt{
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            width: 300px;
            height: 270px;
            background-color: #EEEBEB;
            padding: 30px 0 20px 0;
            margin: 20px 20px;
            border-radius: 40px;
            -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.42); 
            box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.42);
            img{
                margin: 0 0 10px 0;
                width: 100px;
            }
        }
        .card1{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
        }
        .card2{
            margin: 0 50px;
        }

        .img1{
        
            img{
                width: 85px;
            }
        }

    }

    .Beneficios{
        width: 100%;
        min-height: 600px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        img{
            position: absolute;
            right:0;

            height: 600px;
        }

        .texto{
            transform: translate(100px, -80px);
                h2{
                    display: flex;
                    align-items: flex-start;
                    line-height: 1;
                }
        }

        .CardIco{
            transform: translate(110px, -20px);
            display: flex;
            gap: 50px;
            .Ico{   
                    width: 130px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start ;
                    align-items: center;
                    text-align: center;
                    
                    .fundo{
                            width: 130px;
                            height: 130px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color:#14D895 ;
                            border-radius: 100px;
                            margin: 0 0 20px 0;
                    }
                    img{
                        width: 70px;
                        height: 70px;
                        position: relative;
                    }
                    .img6{
                        transform: translate(-5px, 0);
                    }
            }

            .ico6{
               
                    img{
                        height: 70px;
                    }
               
            }
        }
    }



    @media (max-width: 540px){
        .Container{
            padding: 50px 0 100px 0;
            height: 100%;
            max-height: 100%;

            h2{
                font-size: 26px;
                width: 150px;
                margin: 0 0 20px 0;
                transform: translate(0px, -20px);
                main{
                    width: 23px;
                    height: 23px;
                    margin:0px 10px 0 0;
                    border-radius: 100px;
                    border: 5px solid #14D895;
                }
                b{
                    margin: 0px;
                    position: absolute;
                    transform: translate(0px, 28px);
                    padding: 0;
                }
            }
            h3{
                font-size: 22px;
                main{
                width: 20px;
                height: 20px;
                margin: 0 -5px 0 0;
                border-radius: 100px;
                border: 5px solid #14D895;
            }
            }
        }
        .cards{
            flex-direction: column;
            height: 100%;
            .txt{
                width: 260px;
                height: 220px;
                padding: 30px 10px;
                h5{
                    font-size: 14px;
                }

                img{
                    margin: 0 0 20px 0;
                    width: 60px;
                }
            }

            .card2{
                height: 100%;
                margin: 20px 0;
            }
        }

        .Beneficios{
            width: 100%;
            min-height:100%;
            padding: 0px 0 110px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            img{
                position: absolute;
                right:0;
                opacity: 0.3;
                height: 600px;
            }
            .texto{
                transform: translate(10px, -60px);
                    h2{
                        display: flex;
                        align-items: flex-start;
                        line-height: 1;
                        font-size: 26px;
                        width: 300px;
                        main{
                            width: 20px;
                            height: 20px;
                            margin: 2px -10px 0 0;
                            border-radius: 100px;
                            border: 5px solid #14D895;
                        }
                        p{
                            transform: translate(-10px ,0);
                        }
                    }
            }
            .CardIco{
                transform: translate(0px, -20px);
                display: flex;
                justify-content: center;
                gap: 10px;
                flex-wrap: wrap;
                width: 100%;
                
                .break {
                    flex-basis: 100%;
                    height: 0;
                }
                
                .Ico{   
                        width: 140px;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-start ;
                        align-items: center;
                        text-align: center;
                        
                        .fundo{
                                width: 100px;
                                height: 100px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background-color:#14D895 ;
                                border-radius: 100px;
                                margin: 0 0 10px 0;
                        }
                        h5{
                            font-weight: 500;
                            font-size: 14px;
                        }
                        img{
                            width: 50px;
                            height: 50px;
                            opacity: 1;
                            position: relative;
                        }
                        .img6{
                            transform: translate(-7px, 0);
                        }
                }

                .ico6{
                        img{
                            height: 45px;
                        }
                
                }
                .ico7{
                        img{
                            
                            height: 45px;
                        }
                
                }
            }
        }
    }
`
