import styled from "styled-components";

import back from './img/back.png'
import back3 from './img/back3.png'



export const StyleTela2 = styled.div`
        width: 100%;
        min-height: 500px;
        background-image: url(${back});
        background-position: center left;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
    main{
            width: 50px;
            height: 50px;
            margin: 12px 30px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }

    .Text{
        transform: translate(100px, 0 );
        
        h1{
            display: flex;
            line-height: 1.1;
            color: #fff;
        }
        h3{
            color: #fff;
        }
        button{
            background-color: #14D895;
            border: none;
            transform: translate(85px, 0);
            padding: 15px 40px;
            border-radius: 30px;
            a{
                text-decoration: none;

            }
        }
    }

    @media (max-width: 540px){

        width: 100%;
        min-height: 400px;
        background-image: url(${back3});
        background-position: right;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        -webkit-box-shadow: inset 2px 15px 300px 14px rgba(27,31,107,1);
        -moz-box-shadow: inset 2px 15px 300px 14px rgba(27,31,107,1);
        box-shadow: inset 2px 15px 300px 14px rgba(27,31,107,1);


        .Text{
        transform: translate(0px, 0 );
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h1{
                display: flex;
                line-height: 1.1;
                color: #fff;
                font-size: 30px;
                margin: 0 0 130px 0;

                main{
                    width: 25px;
                    height: 25px;
                    margin: 5px 10px 0 0;
                    border-radius: 100px;
                    border: 5px solid #14D895;
                }
            }
            h3{
                color: #fff;
            }
            button{
                background-color: #14D895;
                border: none;
                transform: translate(0px, 0);
                padding: 15px 40px;
                margin: 50px 0 0 0;
            
                a{
                    text-decoration: none;

                }
            }
        }
    }
`