import styled from "styled-components";

export const Foo =styled.div`
    width: 100%;

    min-height: 521px;
    background-color: #1B1F6B;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all .50s ease;
    padding: 50px 0px;

    .ContBox{
        display: flex;
        justify-content: center;
        align-items: flex-start;
    }
    .box{
        width: 300px;
        color: #fff;
        .main{
            position: absolute;
            border-radius: 0 20px 20px 0;
            width: 90%;
            height: 10px;
            margin: -30px 0 0 0;
            left: 0;
            background-color: #fff;
            
            main{
                position: absolute;
                right: -50px;
                width: 25px;
                height: 25px;
                transform: translate(-10px, -10px);
                border-radius: 100px;
                border: 5px solid #14D895;
            }
        }
        a{
            color: #FFf;
        }
        h1{
            color: #13E59A;
            margin: 20px 0 50px 0;
            font-size: 20px;
        }
        .h1{
            margin: 0px 0 50px 0 ;
        }
        h4{
            font-weight: 300;
            font-size:18px;
        }
        button{
            background-color:#7CCD00 ;
            padding: 15px 25px;
            border: none;
            outline: none;
        
            a{
                text-decoration: none;
                color: #fff;
                font-weight: 700;
                font-size: 18px;
            }
        }
    }    
    .start{
        margin: 30px 0 0 0;
        width: 1200px;
        padding: 30px 100px;
        background-color: #141752;
        display: flex;
        flex-direction: column;
        justify-content: center;
        h3{
            color: #fff;
            margin: 0 0 20px 0;
        }
        .Camp{
            display: flex;
            input{
                width: 760px;
                height: 70px;
                background-color: #E2EDF8;
                border: none;
                outline: none;
                padding: 0 0 0 20px ;
                margin: 0 30px 0 0;

                ::placeholder{
                    font-size: 20px;
                }
            }
        }
        button{
            background-color: #13E59A;
            padding: 10px 20px;
            font-size: 24px;
            border-radius: 50px;
            border: none;
            display: flex;
            justify-content: center;
            
            h3{
                margin: 0;
                color: #1B1F6B;
            }
            a{
                color: #fff;
                
                text-decoration: none;
            }
        }
    }
    .Redes{
        margin: 10px 0;
        display: flex;
        img{
            width: 50px;
        }
        .ico{
            margin: 10px;
        }
    }
    .logo{
        display: flex;
        width: 1200px;
        align-items: center;
        justify-content: space-between;

    }




















    @media (max-width: 850px){
        .box1, .box2, .box3{
            width: 200px;
        }
        .start{
            padding: 30px 50px;
        }
        
    }
    @media (max-width: 750px){
        .ContBox{
            flex-direction: column;
        }
        .box1, .box2, .box3{
            width: 300px;
            margin: 20px 0 ;
        }
        .start{
            padding: 30px 20px;
        }
        
    }
    @media (max-width: 670px){
        .box1, .box2, .box3{
            width: 300px;
            margin: 20px 0 ;
        }
        .start{
            padding: 30px 20px;
            .Camp{
                input{
                    width: 300px;
                }
            }
        }
        
    }
    @media (max-width: 520px){
        .box{
            width: 300px;
            margin: 0 0 40px 0;
            color: #fff;
            .main{
                display: none;
                main{
                    position: absolute;
                    right: -50px;
                    width: 25px;
                    height: 25px;
                    transform: translate(-10px, -10px);
                    border-radius: 100px;
                    border: 5px solid #14D895;
                }
                }
                h1{
                color: #13E59A;
                margin: 20px 0 0px 0;
                font-size: 30px;
                }
                h4{
                font-weight: 300;
                font-size:18px;
                }
                .h1{
                margin: 0px 0 0px 0 ;
                }
        } 
        .start{
            margin: -20px 0 0 -10px;
            width: 300px;
            padding: 0;
            background-color: transparent;
            display: flex;
            flex-direction: column;
            justify-content: center;
        h3{
            color: #fff;
            font-weight: 800;
            text-align: left;
            margin: 0 0 10px 0;
        }
        .Camp{
            display: flex;
            flex-direction: column;
            input{
                width: 100%;
                height: 40px;
                background-color: #E2EDF8;
                border: none;
                outline: none;
                padding: 0 0 0 10px ;
                margin: 0 30px 20px 0;

                ::placeholder{
                    font-size: 16px;
                }
            }
        }
        button{
            background-color: #13E59A;
            padding: 10px 20px;
            width: 200px;
            font-size: 24px;
            border-radius: 50px;
            border: none;
            h3{
                margin: 0;
                color: #1B1F6B;
            }
            a{
                color: #1B1F6B;
                
                text-decoration: none;
            }
        }
        }
        .Redes{
            margin: 0 0px 0 -5px;
            display: flex;
            img{
                width: 40px;
            }
            .ico{
                margin: 10px;
            }
    }
    }

`