import styled from 'styled-components';

export const BtnStart = styled.div`
  
  button{
        width: 190px;
        height: 50px;
        background-color: #13E59A;
        border: none;
        border-radius: 50px;
        -webkit-box-shadow: 0px 10px 13px -7px #000000, -11px 23px 50px -40px rgba(0,0,0,0); 
        box-shadow: 0px 10px 13px -7px #000000, -11px 23px 50px -40px rgba(0,0,0,0);
        a{
            text-decoration: none;
            color: #fff;
            font-size: 14px;
            font-weight: 500;
        }
        :hover{
            background-color: rgba(225,225,225, 0.5);
        }
        @media (max-width: 670px){
          width: 130px;
          height: 40px;
          a{
            font-size: 16px;
          }
        }
  }
`;
