import styled from "styled-components";

import Fundo from './img/Back.png'
import Final from './img/final.png'

export const StyleHome = styled.div`
.Cont{
        background-image: url(${Fundo});
        
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
}
.Container{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 991px;
        max-height: 100vh;
}
.text{
    transform: translate(0px, 0);
    width: 600px;
    padding: 0 0 0 80px;
    
    h1{
        color: #fff;
    }
    h3{
        color: #fff;
        font-weight: 400;
        padding: 0 0px 0 0 ;
    }

    button{
        margin: 40px 0 0 0 ;
        background-color:#14D895 ;
        padding: 10px 30px ;
        border: none;
        border-radius: 20px;
        -webkit-box-shadow: 0px 10px 13px -7px #fff, 5px 5px 15px 5px rgba(0,0,0,0); 
            box-shadow: 0px 10px 13px -7px #fff, 5px 5px 15px 5px rgba(0,0,0,0);
        a{
            text-decoration: none;
            color: #fff;
            font-size: 22px;
            font-weight: bold;
        }
    }
}


.final{
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    .IMG{
        width: 100%;
        max-width: 1140px;
        height: 100%;
        background-image: url(${Final});
        background-size: cover;
        background-repeat: no-repeat;
    }
}




@media (max-width: 540px){
    .text{
    transform: translate(0px, 0);
    width: 400px;
    padding: 1% 2%;
        h1{
            color: #fff;
            font-size: 50px;
        }
            h3{
                color: #fff;
                text-align: left;
                font-size: 16px;
            }
        button{
            background-color:#14D895 ;
            padding: 10px 20px ;
            border: none;
            border-radius: 20px;
        }
    }     
}
`