import styled from "styled-components";

import back from './img/back.png'

export const StyleHomeSolucao = styled.div`

    width: 100%;
    height: 1000px;
    max-height: 80vh;
    background-image: url(${back});
    background-size: cover;
    background-position: center center;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    h1{
        color: #fff;
        margin: 0 0 0 150px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    main{
            width: 50px;
            height: 50px;
            margin: 0 20px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
    }

    @media (max-width: 540px){
        max-height: 60vh;

        h1{
        color: #fff;
        margin: 0 0 0 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
    }

    main{
            width: 30px;
            height: 30px;
            margin: 0 10px 0 15px;
            border-radius: 100px;
            border: 7px solid #14D895;
    }


    }
`