import styled from 'styled-components';

export const Slide = styled.div`
    width: 600px;
    min-height: 400px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translate(150px, 0);

    .ContSwiper{
        padding: 50px 0 0 0;
        
        display: flex;
        flex-direction: column;
        align-items: flex-start;
                h2{
                    text-align: left;
                    color: #000;
                }
    }
    .swiper{
        max-width: 600px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 100px;
        .swiper-button-prev, .swiper-button-next{
            background-color: #97A3B3;
            width: 60px;
            border-radius: 50px;
            height: 60px;
            ::after{
                font-size: 30px;
                
            }
        }


        .swiper-button-prev{
            
            ::after{
                color: #fff;
                margin: 0 0 0 -5px;
            }
        }
        .swiper-button-next{
            ::after{
                color: #fff;
                margin: 0 0 0 5px;
            }
        }



    }
    .swiper-horizontal{  
    }
    h1{
        margin: 50px 0 0 0;
        transition: .50s;
        font-size: 51px;
        font-weight: 700;
        color: #000;
    }
    .slider{
        
        height: 250px;
        margin: auto;
        position: relative;
        width: 90%;
        max-width: 1820px;
        display: grid;
        place-items: center;
        overflow: hidden;
        .slider-track{
            display: flex;
            justify-content: center;
            align-items: center;
            width: calc(250px *20);
            animation: scroll 40s linear infinite;
        }
        @keyframes scroll {
            0%{
                transform: translatex(0);
            }
            100%{
                transform: translatex(calc(-250px * 10));
            }
        }
    } 
    .Card{
        background-color: #1B1F6B;
        margin: 20px 100px 20px 0;
        width: 160px;
        height: 160px;
        border-radius: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
        user-select: none;

        img{
            
        }

    }














@media (max-width: 890px){
    h1{
        font-size: 31px;
        transition: .50s;
    }
}
@media (max-width: 540px){
    width: 100%;
    min-height: 300px;
    transform: translate(0px, 0px);
    .ContSwiper{
        padding: 10px 0 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
                h2{
                    text-align: left;
                    color: #000;
                    font-size: 32px;
                    
                    main{
                        width: 25px;
                        height: 25px;
                        margin: 0 0px 0 0;
                        border: 5px solid #14D895;
                    }
                }
    }

    .swiper{
        max-width: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 70px;
        .swiper-button-prev, .swiper-button-next{
            background-color: #97A3B3;
            width: 60px;
            border-radius: 50px;
            height: 60px;
            ::after{
                font-size: 30px;
                
            }
        }
        .swiper-button-prev{
            
            ::after{
                color: #fff;
                margin: 0 0 0 -5px;
            }
        }
        .swiper-button-next{
            ::after{
                color: #fff;
                margin: 0 0 0 5px;
            }
        }



    }
}
`;
