import React from 'react'
import { Link } from 'react-router-dom'
import { HashLink } from 'react-router-hash-link';
import { StyleTela2 } from './Style'

const Tela2 = () => {
  return (
    <StyleTela2>
            <div className="Text">
              <h1><main/><b>Nosso Diferencial </b> </h1>
                    <br />

                    
                    <button>
                        <a href=""><HashLink smooth to='/Tecnologia#Diferenciais'><h3>Clique Aqui</h3></HashLink></a>
                    </button>
            </div>
    </StyleTela2>
  )
}

export default Tela2
