import React from 'react'
import { StyleTela3 } from './Style';
import '../../../styles/Main.css'

import THG from './img/THG.png'
import back from './img/Back.png'
import SlideShow from './../../../Components/SlideShow/Index';


const Tela3 = () => {
  return (
    <StyleTela3>
        <div className="img">
            <img src={back} alt="" />
        </div>
      <div className="Text">
            <h2><main/><b>A 123Tele e o Grupo Total Health</b></h2>
            <br />

            <h4>A 123Tele faz parte do Grupo Total Health (THG), uma holding de empresas que conectam pessoas s melhores solues na rea da sade. Com mais de 30 anos de mercado, a THG tem como misso facilitar o acesso  sade de qualidade para todos. <br /><br />

Atualmente, o Grupo Total Health  constitudo por 7 empresas especialistas em medicina, odontologia, medicamentos especiais, programa de benefcios e assistncias:
</h4>

            <img src={THG} alt="" />
            <SlideShow/>

      </div>
            <div className="Img2"/>
    </StyleTela3>
  )
}

export default Tela3
