import styled from "styled-components";

import img from './img/back.png'

export const StyleAp = styled.div`
width: 100%;
min-height: 911px;
background-image: url(${img});
padding: 100px 0;



        main{
            width: 50px;
            height: 50px;
            margin: 12px 10px 0 0;
            border-radius: 100px;
            border: 10px solid #14D895;
        }
        h2{
            display: flex;
            align-items: center;
            
            width: 600px;
            transform: translate(100px, 0px);
        }


        .cards{
            margin: 100px 0 0 0 ;
            .cardsL{
                position: absolute;
                left: 0;
                .cardL{
                    display: flex;
                        align-items: center;
                        width: 700px;
                        padding: 30px;
                        background-color: #fff;
                        z-index: 9999;
                        margin: 20px 0;
                        position: relative;
                        border-radius: 0 50px 50px 0;
                        -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4); 
                            box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4);
                        img{
                            width: 50px;
                            margin: 0 50px 0 100px;
                        }
                    
                    .Resp{
                        width: 500px;
                        margin: 0 0 0 50px;
                        padding: 30px;
                        border-radius: 30px;
                        transform: translate(0, -20px);
                        border:2px solid #1B1F6b;
                    }
                }
            }

            .cardsR{
                position: absolute;
                right: 0;
                .cardR{
                    display: flex;
                    align-items: center;
                    width: 700px;
                    padding: 30px;
                    margin: 20px 0;
                    background-color: #fff;
                    border-radius:  50px 0 0 50px;
                    -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4); 
                        box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4);
                    img{
                        width: 50px;
                        margin: 0 50px 0 50px;
                    }
                }
            }
        }



        @media (max-width: 540px){
            

            main{
            width: 25px;
            height: 25px;
            margin: 2px 10px 0 0;
            border-radius: 100px;
            border: 7px solid #14D895;
            }
            h2{
                display: flex;
                align-items: center;
                max-width: 300px;
                font-size: 28px;
                transform: translate(15px, 0px);
            }


            .cards{
                margin: 100px 0 0 0 ;
            .cardsL{
                position: relative;
                left: 0;
                .cardL{
                    display: flex;
                        align-items: center;
                        width: 290px;
                        padding: 30px;
                        background-color: #fff;
                        z-index: 9999;
                        margin: 20px 0;
                        position: relative;
                        border-radius: 0 50px 50px 0;
                        -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4); 
                            box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4);
                        img{
                            width: 40px;
                            margin: 0 20px 0 10px;
                        }
                        h4{
                            font-size: 14px;
                        }
                    
                    .Resp{
                        width: 280px;
                        margin: 0 0 0 50px;
                        padding: 30px;
                        border-radius: 30px;
                        transform: translate(0, -20px);
                        border:2px solid #1B1F6b;
                    }
                }
            }

            .cardsR{
                position: relative;
                left: 0;
                h4{
                    font-size: 16px;
                    text-align: left;
                }
                .cardR{
                    display: flex;
                    align-items: center;
                    width: 290px;
                    padding: 30px;
                    margin: 20px 0;
                    background-color: #fff;
                    border-radius: 0 50px 50px 0;
                    -webkit-box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4); 
                        box-shadow: 5px 5px 15px 5px rgba(203,203,203,0.4);
                    img{
                        width: 50px;
                        margin: 0 20px 0 0px;
                    }
                }
            }
        }
        }
`