import React from 'react'
import { StyledForm } from './Style';

const Form = () => {
  return (
    <StyledForm>


      <form>
        <h1><main/>Fale Conosco</h1>
        <input type="text" placeholder='Nome Completo' />
        <input type="email" placeholder='E-mail' />
        <input type="tell" placeholder='Telefone | Whatsapp' />
        <label > Sua mensagem</label>
        <textarea name="" id="" cols="30" rows="10" placeholder='Insira Seu texto'></textarea>

        <button>
            <a href=""><h4>Enviar</h4></a>
        </button>
      </form>
    </StyledForm>
  )
}

export default Form
